<template>
	<div class="h-screen w-full d-flex justify-center">
		<div class="w-full pa-4">
			<h2 class="text-2xl mb-4">Register</h2>

			<v-tabs
				v-model="tab"
				class="mb-12 custom-tab-slider"
				background-color="transparent"
				color="primary"
				grow
				variant="outlined"
			>
				<v-tab value="customer">Customer</v-tab>
				<v-tab value="vendor">Vendor</v-tab>
			</v-tabs>

			<v-window v-model="tab">
				<v-window-item value="customer">
					<v-form @submit.prevent="registerCustomer" ref="customerForm">
						<v-text-field
							v-model="customer.name"
							label="Name"
							key="name"
							variant="outlined"
							density="comfortable"
							:error-messages="errors.customer.name"
							@blur="validateField(customer, 'name', errors.customer)"
							required
						/>
						<v-text-field
							v-model="customer.email"
							label="Email"
							variant="outlined"
							density="comfortable"
							:error-messages="errors.customer.email"
							@blur="validateField(customer, 'email', errors.customer)"
							required
						/>
						<v-text-field
							v-model="customer.password"
							label="Password"
							variant="outlined"
							density="comfortable"
							:type="!markers.customer.password ? 'text' : 'password'"
							:append-inner-icon="
								markers.customer.password ? 'mdi-eye-off' : 'mdi-eye'
							"
							@click:append-inner="toggleMarker('customer.password')"
							:error-messages="errors.customer.password"
							@blur="validateField(customer, 'password', errors.customer)"
							@input="
								passwordStrength.customer = getPasswordStrength(customer.password)
							"
							required
						/>
						<small
							v-if="passwordStrength.customer"
							:style="{ color: getColor(passwordStrength.customer) }"
						>
							Strength: {{ passwordStrength.customer }}
						</small>

						<v-text-field
							v-model="customer.confirmPassword"
							label="Confirm Password"
							variant="outlined"
							density="comfortable"
							:type="!markers.customer.confirmPassword ? 'text' : 'password'"
							:append-inner-icon="
								markers.customer.confirmPassword ? 'mdi-eye-off' : 'mdi-eye'
							"
							@click:append-inner="toggleMarker('customer.confirmPassword')"
							:error-messages="errors.customer.confirmPassword"
							@blur="validateField(customer, 'confirmPassword', errors.customer)"
							required
						/>

						<v-row align="center" class="mb-4 px-2">
							<v-col cols="1" class="d-flex justify-center px-2">
								<v-checkbox
									v-model="customer.agree"
									hide-details
									density="compact"
									class="mt-0"
									:ripple="false"
									@input="validateField(customer, 'agree', errors.customer)"
								/>
							</v-col>
							<v-col cols="11" class="px-2">
								<span class="text-xs">
									I agree to Zuchy
									<router-link
										to="/about/terms-of-service"
										class="text-primary font-weight-medium"
										>Terms of Service</router-link
									>
									and
									<router-link
										to="/about/privacy-policy"
										class="text-primary font-weight-medium"
										>Privacy Policy</router-link
									>.
								</span>
							</v-col>
						</v-row>

						<v-btn
							type="submit"
							color="primary"
							block
							class="my-4"
							rounded="xs"
							size="large"
							:disabled="!isFormValid(customer, errors.customer, touched.customer)"
							>Register as Customer</v-btn
						>
						<p class="text-sm text-center mb-4">
							Already Have An Account?
							<router-link to="/auth/login" class="text-primary font-weight-medium"
								>Login</router-link
							>
						</p>
					</v-form>
				</v-window-item>

				<v-window-item value="vendor">
					<v-form @submit.prevent="registerVendor" ref="vendorForm">
						<v-text-field
							v-model="vendor.name"
							label="Business Name"
							variant="outlined"
							density="comfortable"
							:error-messages="errors.vendor.name"
							@blur="validateField(vendor, 'name', errors.vendor)"
							required
						/>
						<v-text-field
							v-model="vendor.email"
							label="Email"
							variant="outlined"
							density="comfortable"
							:error-messages="errors.vendor.email"
							@blur="validateField(vendor, 'email', errors.vendor)"
							required
						/>
						<v-text-field
							v-model="vendor.password"
							label="Password"
							variant="outlined"
							density="comfortable"
							:type="!markers.vendor.password ? 'text' : 'password'"
							:append-inner-icon="markers.vendor.password ? 'mdi-eye-off' : 'mdi-eye'"
							@click:append-inner="toggleMarker('vendor.password')"
							:error-messages="errors.vendor.password"
							@blur="validateField(vendor, 'password', errors.vendor)"
							@input="passwordStrength.vendor = getPasswordStrength(vendor.password)"
							required
						/>
						<small
							v-if="passwordStrength.vendor"
							:style="{ color: getColor(passwordStrength.vendor) }"
						>
							Strength: {{ passwordStrength.vendor }}
						</small>

						<v-text-field
							v-model="vendor.confirmPassword"
							label="Confirm Password"
							variant="outlined"
							density="comfortable"
							:type="!markers.vendor.confirmPassword ? 'text' : 'password'"
							:append-inner-icon="
								markers.vendor.confirmPassword ? 'mdi-eye-off' : 'mdi-eye'
							"
							@click:append-inner="toggleMarker('vendor.confirmPassword')"
							:error-messages="errors.vendor.confirmPassword"
							@blur="validateField(vendor, 'confirmPassword', errors.vendor)"
							required
						/>

						<v-row align="center" class="mb-4 px-2">
							<v-col cols="1" class="d-flex justify-center px-2">
								<v-checkbox
									v-model="vendor.agree"
									hide-details
									density="compact"
									class="mt-0"
									:ripple="false"
									@input="validateField(vendor, 'agree', errors.vendor)"
								/>
							</v-col>
							<v-col cols="11" class="px-2">
								<span class="text-xs">
									I agree to Zuchy
									<router-link
										to="/about/terms-of-service"
										target="_blank"
										class="text-primary font-weight-medium"
										>Terms of Service</router-link
									>
									and
									<router-link
										to="/about/privacy-policy"
										target="_blank"
										class="text-primary font-weight-medium"
										>Privacy Policy</router-link
									>.
								</span>
							</v-col>
						</v-row>

						<v-btn
							type="submit"
							color="primary"
							class="my-4"
							rounded="xs"
							size="large"
							block
							:disabled="!isFormValid(vendor, errors.vendor, touched.vendor)"
							>Register as Vendor</v-btn
						>
						<p class="text-sm text-center mb-4">
							Already Have An Account?
							<router-link to="/auth/login" class="text-primary font-weight-medium"
								>Login</router-link
							>
						</p>
					</v-form>
				</v-window-item>
			</v-window>
		</div>
	</div>
</template>

<script setup>
import { ref, reactive } from "vue";
import { useRouter } from "vue-router";
import checkPasswordStrength from "check-password-strength";

import { useStore } from "@/store";

const router = useRouter();
const store = useStore();

const tab = ref("customer");

const customer = reactive({
	name: "",
	email: "",
	password: "",
	confirmPassword: "",
	agree: false,
});

const vendor = reactive({
	name: "",
	email: "",
	password: "",
	confirmPassword: "",
	agree: false,
});

const markers = reactive({
	customer: {
		password: true,
		confirmPassword: true,
	},
	vendor: {
		password: true,
		confirmPassword: true,
	},
});

function toggleMarker(marker) {
	const [section, field] = marker.split(".");
	markers[section][field] = !markers[section][field];
}

const errors = reactive({
	customer: { name: "", email: "", password: "", confirmPassword: "" },
	vendor: { name: "", email: "", password: "", confirmPassword: "" },
});

const touched = reactive({
	customer: {
		name: false,
		email: false,
		password: false,
		confirmPassword: false,
		agree: false,
	},
	vendor: {
		name: false,
		email: false,
		password: false,
		confirmPassword: false,
		agree: false,
	},
});
const passwordStrength = reactive({
	customer: "",
	vendor: "",
});

function getPasswordStrength(password) {
	const strength = checkPasswordStrength.passwordStrength(password).value;
	return strength;
}

function getColor(strength) {
	switch (strength) {
		case "Too weak":
			return "red";
		case "Weak":
			return "orange";
		case "Medium":
			return "goldenrod";
		case "Strong":
			return "green";
		default:
			return "gray";
	}
}

function isValidEmail(email) {
	return /\S+@\S+\.\S+/.test(email);
}

function validateField(form, field, targetErrors) {
	touched[tab.value][field] = true;

	const value = form[field];
	let error = "";

	if (field === "name") {
		error = !value ? "Name is required" : "";
	} else if (field === "email") {
		error = !value
			? "Email is required"
			: !isValidEmail(value)
			? "Invalid email"
			: "";
	} else if (field === "password") {
		error = value.length < 6 ? "Minimum 6 characters" : "";
	} else if (field === "confirmPassword") {
		error = value !== form.password ? "Passwords do not match" : "";
	} else if (field === "agree") {
		error = !value ? "You must agree to the terms" : "";
	}

	targetErrors[field] = error;
}

function isFormValid(form, targetErrors, touchedFields) {
	const fields = Object.keys(form);
	return fields.every(
		(field) => touchedFields[field] && !validateFieldSilently(form, field)
	);
}

function validateFieldSilently(form, field) {
	const value = form[field];
	if (field === "name") return !value;
	if (field === "email") return !value || !isValidEmail(value);
	if (field === "password") return value.length < 6;
	if (field === "confirmPassword") return value !== form.password;
	if (field === "agree") return !value;
	return false;
}
//

async function registerCustomer() {
	if (isFormValid(customer, errors.customer, touched.customer)) {
		const result = await store.registerCustomer(customer);
		if (result) store.login(customer.email, customer.password);
	}
}

async function registerVendor() {
	if (isFormValid(vendor, errors.vendor, touched.vendor)) {
		const result = await store.registerVendor(vendor);
		if (result) store.login(vendor.email, vendor.password);
	}
}
</script>

<style scoped>
::v-deep(.custom-tab-slider .v-tab__slider) {
	width: 30% !important;
	margin-left: auto !important;
	margin-right: auto !important;
	left: 0 !important;
	right: 0 !important;
}
</style>
